.container{
	max-width: 1920px;
	width: 100%;
	padding: 0 15px;
	margin: 0 auto;
	.with--aside{
		margin-left: 375px;
		position: relative;
		&:before,
		&:after{
			content: '';
			display: table;
			clear: both;
		}
	}
	.no--aside{
		padding-bottom: 50px;
		.aside.aside-left{
			float: none;
			margin-left: 0;
		}
		.section-content{
			float: none;
		}
	}
}
.main.main-good{
	.no--aside{
		.left-side--catalog_content{
			display: none !important;
		}
		.toggle-link.toggle-link--active + .left-side--catalog_content{
			left: 15px;
			display: block !important;
		}
	}
}
.aside-left{
	float: left;
	width: 345px;
	margin-left: -375px;
}
.section-right{
	float: right;
	// width: 530px;
}
.jcarousel-wrapper{
	margin-right: 310px;
}
.block-goods--wrapp.block-goods-list--wrapp{
	width: 100%;
}

.block-goods--wrapp,
.block-brands--wrapp{
	width: 20%;
}

.footer{
	.nav-footer{
		width: 65%;
		float: left;
		.nav-footer-col{
			width: 33.3331%;
			float: left;
		}
	}
	.contacts-block{
		width: 36%;
		float: left;
		.col{
			width: 50%;
			float: left;
		}
	}
}
.col-fix-left-panel{
	width: 65%;
	float: left;
}

.col-fix-right-panel{
	width: 35%;
	float: left;
}

////Login
.reg-col{
	width: 50%;
	float: left;
	&:nth-child(2n+1){
		padding-right: 10px;
	}
	&:nth-child(2n){
		padding-left: 10px;
	}
}

.tab-content > .tab-pane{
	width: 90%;
	.reg{
		width: 100%;
	}
	form{
		width: 50%;
		padding-right: 15px;
		float: left;
	}
	.social-sign{
		padding-left: 15px;
		width: 50%;
		float: left;
	}
}
