// 1440px
@media only screen and (max-width: 1440px) and (min-width: 1066px) {
	.nav-bar{
		&:not(.open) {
			width: 55px;
			.nav-bar--item {
				white-space: nowrap;
				width: 55px;
				height: 60px;
				overflow: hidden;
				margin: 0;
				position: relative;
				&:not(.nav-bar--toggle) {
					padding: 0 15px;
				}
				&:hover:not(.nav-bar--toggle) {
					-webkit-transition: all .2s ease-out;
					-moz-transition: all .2s ease-out;
					-o-transition: all .2s ease-out;
					transition: all .2s ease-out;
					white-space: normal;
					width: 230px;
					margin-left: -175px;
					background-color: rgba(232,232,232,.9);
					i {
						right: -20px;
						left: auto;
						display: inline;
					}
					a {
						padding-right: 50px;
						padding-left: 0;
						span {
							display: block;
						}
						> span {
							float: right;
							text-align: right;
						}
						> span,.amount {
							display: block;
						}
					}
				}
				a{
					padding-left: 50px;
				}
				&.nav-bar--toggle span {
					display: none;
				}
				i,span,.amount {
					float: none;
					display: inline-block;
				}
				.amount {
					margin-right: 5px;
				}
				i{
					width: 30px;
					position: absolute;
					left: 0;
					top: 0;
					margin-left: 0;
					text-align: center;
					&:before {
						font-size: 12px;
					}
				}
			}
		}
		z-index: 1000;
		position: relative;
		font-weight: 700;
		&.open {
			.nav-bar--toggle {
				transform: rotate(180deg);
			}
		}
		&--item {
			&.nav-bar--toggle {
				display: block;
				padding-top: 13px;
				padding-left: 10px;
			}
			height: 60px;
			overflow: hidden;
			margin: 0;
			padding: 0 15px;
			position: relative;
			&_single span {
				margin-top: 24px;
			}
			&__wrapper {
				margin-top: 16px;
			}
			a{
				padding-left: 50px;
			}
			i{
				width: 30px;
				position: absolute;
				left: 0;
				top: 0;
				margin-left: 0;
				text-align: center;
				&:before {
					font-size: 12px;
				}
			}
		}
	}
}
