@import "varibles/sprite.scss";
@import "varibles/color.scss";
@import "varibles/type.scss";
@import "varibles/varibles.scss";
@import "varibles/carousel.scss";
@import "varibles/cart.scss";
@import "varibles/filter.scss";
@import "varibles/good.scss";
@import "varibles/fancybox.scss";
@import "varibles/inner-style.scss";
@import '../../bower_components/sass-mq/_mq.scss';
@import 'fotorama.scss';


$ieSprite: '.lt-ie9' !default;
$sprite: map-get($icons, sprite) !default;
$baseFontSize: 16px !default;

@import "varibles/mixins.scss";
*{
	box-sizing: border-box;
}
.hidden-lg{
	display: none;
}
.pull-left{
	float: left;
}

.wrapper{
	font-family: 'Open Sans';
}
.top-bar{
	line-height: 14px;
	padding: 5px 0 2px 0;
	z-index: 2;
	.select{
		vertical-align: -6px;
		line-height: 14px;
		padding-right: 20px;
	}
	.menu-icon{
		display: none;
		margin-top: 6px;
		margin-right: 20px;
	}
	a{
		color: #fff;
	}
	a,span{
		vertical-align: -5px;
		line-height: 14px;
		padding-right: 20px;
	}
	&--menu{
		float: left;
	}
	&--menu_wrap{
		z-index: 1000;
	}
	&--logIn{
		a{
			display: inline-block;
		}
		margin-top: 3px;
		cursor: pointer;
		float: right;
	}
}
.social-block{
	a{
		margin-left: 20px;
		font-size: 26px;
	}
}
.braedcrumbs{
	margin-bottom: 12px;
	color: $DarkBG;
	line-height: 25px;
	font-size: 12px;
	a{
		font-size: 12px;
		color: $DarkBG;
		white-space: nowrap;
	}
	.marker{
		color: $DarkBG;
		font-size: 25px;
		vertical-align: middle;
	}
	span{
		font-size: 12px;
		color: $BorderColor;
		white-space: nowrap;
	}
}
.inside{
	h5{
		margin-bottom: 10px;
		line-height: 40px;
	}
}
.logo-icon{
	float: left;
	&:before{
		font-size: 5px;
	}
}
.logo-mobile{

	i {
		margin-right: 270px;
		float: none;
	}
}
.amount{
	font-weight: 600;
	font-size: 18px;
	border-radius: 3px;
	margin: 25px 0 0 10px;
	float: right;
	display: block;
	padding: 3px 5px;
}
