.search{
	position: absolute;
	top: 0;
	width: 100%;
	min-width: 320px;
	left: 0;
	background-color: $BlackBG;
	padding:  15px 20px 30px;
	z-index: 1001;
	display: none;
	.toggle-search{
		color: #fff;
		padding-bottom: 15px;
		text-align: right;
		margin-right: 10px;
		cursor: pointer;
		&:hover{
			color: darken(#fff, 30%);
		}
	}
	.wrap{
		margin-right: 85px;
	}
	input{
		border-radius: 3px;
		height: 50px;
		width: 100%;
		background-color: darken($BlackBG, 5%);
		padding: 0 15px;
		font-size: 18px;
		font-weight: 300;
		color: lighten($BlackBG, 35%);
		border: none;
	}
}
.button-search{
	border-radius: 3px;
	margin-top: 30px;
	background-color: darken($BlackBG, 5%);
	padding: 10px 11px;
	font-size: 12px;
	margin-left: 20px;
	float: right;
	cursor: pointer;
	&:hover{
		background-color: darken($BlackBG, 8%);
		&:before{
			@include sprite(searchRedHover);
		}
		&:active{
			@include sprite(searchRedActive);
		}
	}
	&:before{
		@include sprite(searchRed);
	}
}
.nav-bar{
	float: right;
	width: 280px;
	background-color: $Grey;
	&--item{
		&.nav-bar--toggle {
			cursor: pointer;
			display: none;
			background-color: $DarkBG;
			&:before {
				content: '';
				@include sprite(prevRed);
			}
		}
		margin: 0 15px;
		display: block;
		&:not(:last-child) {
			border-bottom: solid 1px #fff;
		}
		&_single{
			span{
				margin-top: 29px;
			}
		}
		a,span{
			color: $redColor;
			cursor: pointer;
		}
		&:not(:first-child){
			// @extend .border-icons;
		}
		&.active,
		&.nav-bar--item_link{
			a{
				cursor: pointer;
			}
		}
		i{
			display: inline-block;
			float: left;
			margin-top: 20px;
			margin-right: 15px;
			margin-left: 15px;
			&:before{
				font-size: 15px;
			}
		}
		.user-icon:before{
			font-size: 16px;
			@include sprite(userRed);
		}
		.catalog-icon:before{
			font-size: 16px;
			@include sprite(catalogRed);
		}
		&:hover{
			color: $redColorHover;
			a,span{
				color: $redColorHover;
				cursor: pointer;
			}
			.phone-icon:before{
				@include sprite(phoneRedHover);
			}
			.favorite-icon:before{
				@include sprite(heartRedHover);
			}
			.cart-icon:before{
				@include sprite(cartRedHover);
			}
			.user-icon:before{
				@include sprite(userRedHover);
			}
			.catalog-icon:before{
				@include sprite(catalogRedHover);
			}
		}
		&:active{
			a,span{
				color: $redColorActive;
				cursor: pointer;
			}
			.phone-icon:before{
				@include sprite(phoneRedActive);
			}
			.favorite-icon:before{
				@include sprite(heartRedActive);
			}
			.cart-icon:before{
				@include sprite(cartRedActive);
			}
			.user-icon:before{
				@include sprite(userRedActive);
			}
			.catalog-icon:before{
				@include sprite(catalogRedActive);
			}
		}
		&__wrapper{
			margin-top: 21px;
			float: left;
		}
		a{
			position: relative;
			display: block;
			height: 70px;
			&:hover{
				text-decoration: none;
			}
			&:after{
				content: " ";
				display: inline-block;
				vertical-align: middle;
				height: 100%;
			}
			span{
				display: block;
				> span {
					margin-top: 5px;
				}
				&.amount{
					float: left;
					color: $redColor;
					background-color: $BorderColor;
				}
			}
		}
	}
}

//MAIN
.action_content {
	margin: 15px 0 30px;
	a {
		display: block;
		
		font-size: 21px;
	}
}
.main{
	padding-top: 30px;
	padding-bottom: 60px;
	background-color: $MainBG;
	&.inside{
		background-color: #fff;
	}
	.left-side{
		background-color: #fff;
		padding: 11px 25px 0;
		margin-bottom: 30px;
		@extend .default-boreder;
		&_title{
			padding: 11px 25px 15px;
			margin: -11px -25px 0;
			background-color: $DarkBG;
			h6{
				color: #fff;
			}
			.catalog-icon{
				margin-top: 2px;
				font-size: 10px;
			}
		}
		&--wrapp{
			padding-top: 15px;
		}
		&_img{
			text-align: center;
			margin-right: 10px;
			width: 150px;
			height: 127px;
			float: left;
			@extend .default-boreder;
			overflow: hidden;
			position: relative;
			img{
				max-width: 150px;
				height: auto;
				@extend .center.aboslute;
			}
		}
		&_content{
			width: 188px;
			float: right;
			margin-left: 10px;
			a{
				margin-bottom: 15px;
				display: block;
				text-decoration: underline;
				&:hover{
					text-decoration: none;
				}
			}
		}
	}
	.left-side--catalog{
		h6{
			float: left;
		}
		i{
			float: right;
		}
		&_content{
			padding: 0 0 7px;
		}
		&_list{
			margin-right: -25px;
			@extend .border--left-side;
			li{
				padding-right: 25px;
				position: relative;
				line-height: 44px;
				&:hover{
					ul.hide{
						display: block;
					}
				}
			}
		}
		&_sub{
			margin-top: 18px;
			li{
				line-height: 20px;
			}
		}
		ul.hide{
			display: none;
			left: 318px;
			top: 0;
			padding-left: 25px;
			padding-right: 25px;
			width: 340px;
			background-color: #fff;
			z-index: 1000;
			position: absolute;
			box-shadow: 0 0 10px rgba(0,0,0,.4);
			li{
				font-size: 12px;
				line-height: 28px;
				> span{
					float: right;
					font-size: 12px;
				}
				&.title{
					font-size: 14px;
					font-weight: 600;
				}
				// white-space: nowrap;
			}
			.left-side--catalog_slider{
				@extend .default-border--left;
				background-color: #fff;
				position: absolute;
				right: -340px;
				z-index: 10000;
				width: 340px;
				.jcarousel{
					max-height: 443px;
					height: 443px;
					li{
						height: 443px;
					}
				}
				top: 0;
				.jcarousel-wrapper{
					position: relative;
					margin-left: 0;
					margin-right: 0;
					left: 0 !important;
					width: 560px;
					.jcarousel-control{
						top: 100px;
						transform: translateY(0);
					}
					.block-goods_img{
						margin: 25px 0 0;
						overflow: visible;
					}
					.block-goods--wrapp{
						float: none;
						width: 340px;
						padding-bottom: 0;
						.block-goods--inCart{
							padding-top: 0;
						}
						.block-goods_hover{
							opacity: 1;
							z-index: 10010;
						}
					}
					.defaultBtn{
						line-height: 18px;
					}
					.auxiliary-buttons{
						margin-top: 5px;
					}
				}
			}
		}
	}
	.left-side--news{
		font-size: 12px;
		.left-side_content {
			width: 300px;
		}
		.left-side--wrapp {
			padding-top: 30px;
		}
		a{
			text-decoration: underline;
			&:hover{
				text-decoration: none;
			}
		}
		&_more{
			margin: 30px -26px -16px;
			li{
				margin-bottom: -1px;
				padding-left: 25px;
				padding-right: 25px;
				@extend .default-boreder;
				a{
					font-weight: 300;
					font-size: 12px;
					line-height: 48px;
				}
				background: $InputBG;
			}
		}
	}
	.left-side-subscrible{
		text-align: center;
		form{
			margin-top: 25px;
		}
		input{
			padding: 5px 23px;
			font-size: 18px;
			width: 100%;
			margin-bottom: 20px;
		}
	}
	.left-side-bunner{
		overflow: hidden;
		padding: 0;
		img{
			width: auto;
			height: 100%;
		}
	}
	.aside-nav{
		left: -15px;
		width: 360px;
		height: 100%;
		position: absolute;
		float: none;
		background-color: $BorderColor;
	}

	.left-side--nav{
		background-color: $BorderColor;
		left: -15px;
		width: 375px;
		margin-bottom: 0;
		padding-right: 0;
		&_content{
			text-align: right;
			li{
				margin-bottom: 10px;
				margin-right: -1px;
				vertical-align: middle;
				line-height: 42px;
				vertical-align: middle;
				&.active,
				&:hover{
						a{
							background-color: #fff;
						}
				}
				a{
					padding-right: 65px;
					padding-left: 15px;
					display: inline-block;
					position: relative;
					font-weight: 400;
					color: $redColor;
					&:hover{
						text-decoration: none;
					}
				}
				i{
					font-size: 12px;
					display: inline-block;
					width: 60px;
					text-align: center;
					&:before{
						display: inline-block;
						vertical-align: middle;
						// right: 20px;
						// top: 50%;
						// transform: translateY(-50%);
						// font-size: 14px;
					}
				}
			}
		}
	}
	.section-right--good{
		padding: 0 15px;
		background-color: $BorderColor;
		position: absolute;
		height: 100%;
		right: -15px;
		float: none;
	}
	.review-right--good{
		display: none;
		.section-right--good_title .price{
			font-size: 31px;
		}
		.col{
			float: left;
			width: 50%;
		}
	}
	.section-right--good,
	.review-right--good{
		.defaultBtn{
			width: 100%;
			margin: 25px 0;
			padding: 16px 0 20px;
			span{
				margin-left: 25px;
				position: relative;
				color: #fff;
			}
			i{
				position: absolute;
				font-size: 8px;
				font-size: 11px;
				top: -2px;
				left: -25px;
			}
		}
		.retings{
			margin: 25px 0;
			float: none;
		}
		.auxiliary-buttons{
			margin-top: 0;
			height: 40px;
			width: 100%;
			float: none;
			a{
				margin-left: 5px;
				padding-left: 25px;
			}
			span{
				margin-left: 5px;
				padding-left: 5px;
				line-height: 16px;
				padding-bottom: 10px;
				vertical-align: top;
			}
			i{
				display: inline-block;
				width: auto;
				font-size: 10px;
				&.order-icon{
					line-height: 10px;
					&:before{
						font-size: 10px;
					}
				}
			}
		}
	}
	.section-right--good_title{
		margin-top: 15px;
		line-height: 42px;
		font-size: 12px;
		color: $GreyBG;
		.price{
			font-size: 18px;
			font-weight: 600;
		}
	}
	.section-right--banner{
		&:first-child{
			margin-bottom: 15px;
		}
		max-height: 162px;
		overflow: hidden;
	}
}
.section-content{
	width: 100%;
	float: left;
}
// center main
.block{
	padding-top: 30px;
	clear: both;
	&-nav{
		color: #fff;
		li{
			float: left;
			width: 20%;
			text-align: center;
		}
		span,
		a{
			padding: 13px 0 16px;
			font-size: 20px;
			line-height: 20px;
			display: block;
			color: #fff;
		}
		a{
			background: $DarkBG;
			&:hover,
			&.active{
				text-decoration: none;
			}
		}
		&-title{
			background: $DarkBG;
			width: 100%;
			span{
				padding-left: 15px;
				padding-right: 15px;
				display: inline-block;
			}
		}
	}
	&-goods,
	&-brands{
		@extend .default-border--left;
		@extend .clearfix-extendet;
	}
	&-goods_hover{
		transition: opacity .4s ease-in-out;
		opacity: 0;
		z-index: -1;
	}
	&-goods--inCart{
		position: absolute;
		width: 100%;
		height: 120px;
		bottom: -120px;
		left: 0;
		background-color: #fff;
		padding: 15px;
		box-shadow: 0 10px 9px -5px rgba(0,0,0,.2);
		@extend .default-border-bottom;
		.button{
			span{
				font-size: 21px;
				position: relative;
				color: #fff;
				display: inline-block;
				i{
					position: absolute;
					font-size: 8px;
					top: 0;
					left: -25px;
				}
			}
		}
	}
	&-goods--wrapp,
	&-brands--wrapp{
		padding: 13px 15px;
		background-color: #fff;
		float: left;
		position: relative;
		@extend .default-border-right;
		@extend .default-border-bottom;
		&:hover{
			.block-goods_hover{
				z-index: 10;
				opacity: 1;
			}
		}
		p{
			height: 45px;
			overflow: hidden;
			line-height: 21px;
			&.price{
				margin-top: 15px;
				font-weight: 600;
				font-size: 18px;
				span{
					font-weight: 300;
					font-size: 12px;
					text-decoration: line-through;
				}
			}
		}
		&_more{
			background: transparent;
			border: none;
			position: relative;
			height: 384px;
			text-align: center;
			padding: 0;
			.center{
				cursor: pointer;
			}
			p{
				font-size: 18px;
				&.sum{
					font-size: 12px;
				}
			}
			.morePlus-icon{
				@include sprite(morePlus);
			}
		}
	}
	&-goods_title{
		.statusBadge{
			color: #fff;
			line-height: 20px;
			float: left;
			padding: 0 26px 2px 4px;
		}
	}
	&-goods_img{
		overflow: hidden;
		position: relative;
		display: block;
		width: 100%;
		max-height: 180px;
		height: 180px;
		margin: 47px 0 25px;
		img{
			@extend .center.aboslute;
			width: auto;
			height: 100%;
		}
	}
	&-brands--wrapp{
		position: relative;
		height: 150px;
		overflow: hidden;
		img{
			width: 70%;
			height: auto;
			@extend .center.aboslute;
		}
	}
	&-carousel-title{
		background-color: #fff;
		text-align: center;
		padding: 10px 0;
		text-transform: uppercase;
		@extend .default-boreder;
	}
	.carousel-goods{
		height: 490px;
		max-height: 490px;
		border-left: solid 1px $BorderColor;
	}
	&-goods-carousel{
		.block-goods--wrapp{
			width: 100%;
		}
	}
}
//Footer
.scroll-up{
	position: fixed;
	right: 50px;
	bottom: 150px;
	@include sprite(up-icon);
	cursor: pointer;
	display: none;
}
.footer{
	background-color: $BlackBG;
	@extend .clearfix-extendet;
	margin-right: 0px;
	margin-left: 0;
	.social-block{
		position: fixed;
		right: 50px;
		bottom: 38px;
		z-index: 10;
		&:hover{
			bottom: 43px;
		}
		a{
			margin-left: 20px;
			font-size: 26px;
		}
	}
	.nav-footer{
		margin-left: -25px;
		padding-left: 25px;
		padding-bottom: 70px;
		background-color: $DarkBG;
		overflow:hidden;
		h6{
			margin-left: 10px;
			margin-top: 10px;
			color: $BorderColor;
			margin-bottom: 30px;
		}
		a{
			color: $BorderColor;
		}
		&-col{
			li{
